// Fade out effect
async function fadeOutEffect(fadeTarget) 
{
    if (fadeTarget.style.display == "none")
        return;

    fadeTarget.className = "fade-out"
    setTimeout(function()
    {
        fadeTarget.style.opacity = "0"
        fadeTarget.style.display = "none"
    }, 300)
}

async function fadeInEffect(fadeTarget, duration) 
{    
    fadeTarget.style.opacity = "1"
    fadeTarget.style.display = "inline"
    fadeTarget.className = "fade-in-" + duration
}

