// Stars
function addStarUp()
{
    const starGeometry = new THREE.SphereBufferGeometry(0.0019, 64, 64)
    const startMaterial = new THREE.MeshBasicMaterial( {color: 0xffffff} )

    const star = new THREE.Mesh(starGeometry, startMaterial)
    const x = THREE.MathUtils.randFloatSpread(8)
    const y = THREE.MathUtils.randFloatSpread(5)
    const z = Math.abs(THREE.MathUtils.randFloatSpread(0))
    star.position.set(x, y, z)

    scene.add(star)
    
    return star
}

function addStarDown()
{
    const starGeometry = new THREE.SphereBufferGeometry(0.0019, 64, 64)
    const startMaterial = new THREE.MeshBasicMaterial( {color: 0xffffff} )

    const star = new THREE.Mesh(starGeometry, startMaterial)
    const x = THREE.MathUtils.randFloatSpread(8)
    const y = - Math.abs(THREE.MathUtils.randFloatSpread(2 * (HOME_TO_ABOUT_MOVE_Y + ABOUT_TO_PROJECTS_MOVE_Y)))
    const z = Math.abs(THREE.MathUtils.randFloatSpread(0))
    star.position.set(x, y, z)

    scene.add(star)
    
    return star
}

