let mouseX = 0;
let mouseY = 0;

let targetX = 0;
let targetY = 0;

const windowHalfX = window.innerWidth / 2;
const windowHalfY = window.innerHeight / 2;


// Mouse Sphere rotation Event
const mouse = new THREE.Vector2()



function onDocumentMouseDown(event)
{
    event.preventDefault();

    let intersectedObjs = raycaster.intersectObjects(objs)
    if (intersectedObjs.length > 0)
        window.open(intersectedObjs[0].object.userData.URL)
}

document.addEventListener("mousedown", onDocumentMouseDown, false)


function onDocumentMouseMove(event)
{
    mouseX = (event.clientX - windowHalfX) 
    mouseY = (event.clientY - windowHalfY)

    // Old intensity 0.00008
    mouseMoveX = (event.clientX - window.innerWidth / 2) * 0.00007
    mouseMoveY = (event.clientY - window.innerHeight / 2) * 0.00007

    mouse.x = event.clientX / sizes.width * 2 - 1
    mouse.y = - (event.clientY / sizes.height) * 2 + 1

    // Light move on mouse move
    //mouseLight.position.x = ((event.clientX / window.innerWidth) * 200 - 100) * 0.5
    //mouseLight.position.y = -((event.clientY / window.innerHeight) * 200 + 100) * 0.5
}

document.addEventListener("mousemove", onDocumentMouseMove)


